import {GetIndexInfo} from "../../net/IndexNet";
import {GetLoginModel} from "../../model/LoginSuccessModel";
import {Const} from "../../utils/Const";
import {Notification} from "../../utils/Notification";
import {Notify} from "../../utils/Notify";
import {UnReadMsgModel} from "../../model/im/UnReadMsgModel";
import {ChatListModel} from "../../model/im/ChatListModel";
import {GetHomeUnReadNumModel,SetHomeModel} from "../../model/HomeModel";
import {CheckCustInfoByQrCode} from "../../net/ConfirmNet";
import {Utils} from "../../utils/Utils";
import {Permission} from "../../utils/Permission";
import {AppUserModel} from "../../model/AppUserModel";

export default {
    data() {
        return {
            newCustomNum: 0,
            arriveVisitNum: 0,
            waitCommissionNum: 0,

            newCustomUnReadNum: 0,
            arriveVisitUnReadNum: 0,
            waitCommissionUnReadNum: 0,

            showRedPoint: false,

            scanModel: {},

            list: [
                {
                    text: "扫码确客",
                    src: "http://cdn.haofang.net/static/xffx-uniapp/index/scan.png",
                    path: "/pagesConfirm/scanConfirmCustomer/scanConfirmCustomer",
                    className: "item_scan"
                },
                {
                    text: "客户",
                    src: "http://cdn.haofang.net/static/xffx-uniapp/index/custom.png",
                    path: "/pagesCustomer/customerList/customerList",
                    className: "item_custom"
                },
                {
                    text: "消息",
                    src: "http://cdn.haofang.net/static/xffx-uniapp/index/msg.png",
                    path: "/pagesMsg/msgList/msgList",
                    className: "item_msg"
                },
                {
                    text: "楼盘",
                    src: "http://cdn.haofang.net/static/xffx-uniapp/index/build.png",
                    path: "/pagesBuild/buildDetail/buildDetail",
                    className: "item_build"
                },
                {
                    text: "佣金",
                    src: "http://cdn.haofang.net/static/xffx-uniapp/index/commission.png",
                    path: "/pagesCommission/commissionManage/commissionManage",
                    className: "item_yj"
                },
                {
                    text: "统计",
                    src: "http://cdn.haofang.net/static/xffx-uniapp/index/tj.png",
                    path: "/pagesStatistics/statisticsAnalysis/statisticsAnalysis",
                    className: "item_tj"
                }
            ]
        }
    },

    onLoad() {
        // 更新消息体NC
        new Notification().addObserver(this, "UpChartMsgChanged", Notify.UpChartMsgChanged.Name);
        // 监听扫码结果
        new Notification().addObserver(this, "ScanConfirmChange", Notify.ScanConfirmChange.Name);
        // 监听自动登陆成功
        new Notification().addObserver(this, "AutoLoginSuccess", Notify.AutoLoginSuccess.Name);
    },

    onShow() {
        this.UpChartMsgChanged();
        // 避免接口再自动登陆时造成token过期
        if (!new AppUserModel().getModel("enterOnLaunch")
            || new AppUserModel().getModel("enterOnLaunch") == 2) {
            this.initData();
        }
    },

    onUnload() {
        new Notification().removeObserverAllNotification(this);
    },

    methods: {
        // 自动登陆成功回调
        AutoLoginSuccess() {
            this.initData();
        },

        // 扫码结果回调
        ScanConfirmChange(name, info) {
            this.scanModel = info;
            setTimeout(() => {
                this.$refs.modal.show();
            }, 1000);
        },
        onModalChanged(val) {
            if (!val) {
                new CheckCustInfoByQrCode({
                    custId: this.scanModel.custId,
                    cityId: this.scanModel.cityId,
                    status: 0
                }).send().then(res => {
                    if (res.errCode !== Const.success) {
                        this.DifferenceApi.showToast(res.errMsg || "操作失败");
                        return;
                    }

                    this.DifferenceApi.showToast(res.errMsg || "操作成功");
                });
                return;
            }

            new CheckCustInfoByQrCode({
                custId: this.scanModel.custId,
                cityId: this.scanModel.cityId,
                status: this.scanModel.status
            }).send().then(res => {
                if (res.errCode !== Const.success) {
                    this.DifferenceApi.showToast(res.errMsg || "操作失败");
                    return;
                }

                this.initData();
                this.DifferenceApi.showToast(res.errMsg || "操作成功");
            });
        },

        UpChartMsgChanged() {
            let listModel = new ChatListModel().getModel() || {};
            let list = listModel[(GetLoginModel.getUserId() || "")] || {};
            let map = new UnReadMsgModel().getModel() || {};
            let allNum = 0;
            for(let key in map) {
                if(!list[key]) continue;

                allNum += Number(map[key]);
            }
            this.showRedPoint = !!(allNum > 0 && Object.keys(list).length > 0);
        },

        initData() {
            new GetIndexInfo({buildId: GetLoginModel.getBuildId()}).send()
                .then(res => {
                    if(res.errCode !== Const.success) {
                        // this.DifferenceApi.showToast(res.errMsg || "获取首页数据失败啦");
                        return;
                    }

                    let data = res.data || {};
                    this.newCustomNum = data.unAuditedCount || 0;
                    this.arriveVisitNum = data.haveVisitedCount || 0;
                    this.waitCommissionNum = data.unSettleCommissionCount || 0;

                    SetHomeModel.setNewCustomNum(data.unAuditedCount || 0);
                    SetHomeModel.setArriveVisitNum(data.haveVisitedCount || 0);
                    SetHomeModel.setWaitCommissionNum(data.unSettleCommissionCount || 0);

                    this.newCustomUnReadNum = GetHomeUnReadNumModel.getNewCustomNum();
                    this.arriveVisitUnReadNum = GetHomeUnReadNumModel.getArriveVisitNum();
                    this.waitCommissionUnReadNum = GetHomeUnReadNumModel.getWaitCommissionNum();
                })
                .catch(err => {
                    // this.DifferenceApi.showToast(err.errMsg || "获取首页数据失败啦");
                });
        },

        // 设置
        navigateSet() {
            uni.navigateTo({
                url: "/pagesSet/index/index"
            });
        },

        // 列表
        navigateItem(item) {
            if (item.path.indexOf("scanConfirmCustomer") >= 0) {
                new Permission().photoPermission(val => {
                    if (val === 1) {
                        uni.showModal({
                            title: "",
                            content: "请打开设置-允许相机权限",
                            showCancel: false,
                            confirmText: "去开启",
                            confirmColor: "#3396fb",
                            success: (res) => {
                                if(res.confirm) {
                                    new Permission().openSet();
                                }
                            }
                        });
                    }

                    if (val === 3) {
                        uni.navigateTo({
                            url: item.path
                        });
                    }
                });
                return;
            }

            uni.navigateTo({
                url: item.path
            });
        },

        // 新客户
        goToNewCustomView(){
            if(this.newCustomNum <= 0) return;

            SetHomeModel.setNewCustomReadNum();
            uni.navigateTo({
                url: '/pagesCustomer/newCustomer/newCustomer'
            });
        },

        // 已到访
        goToArriveVisitView(){
            if(this.arriveVisitNum <= 0) return;

            SetHomeModel.setArriveVisitReadNum();
            uni.navigateTo({
                url: '/pagesCustomer/arriveVisit/arriveVisit'
            });
        },

        // 待结佣
        goToWaitCommissionView(){
            if(this.waitCommissionNum <= 0) return;

            SetHomeModel.setWaitCommissionReadNum();
            uni.navigateTo({
                url: '/pagesCustomer/waitCommission/waitCommission'
            });
        },
    }
}
